<template>
  <div class="Tab" >
  	<!--头部-->
  	<div id="header" style="opacity:0.7;position: fixed;top: 0;z-index: 999;width: 100%;">
  		<mu-appbar >
			  <mu-icon-button slot="left" @click="toggle('drawer')"><i class="fa fa-bars"/></mu-icon-button>
				<mu-icon-button slot="right"  ref="button" @click="toggle('theme')"><i class="fa fa-google-wallet"/></mu-icon-button>
				<mu-popover :trigger="trigger" :open="open" @close="handleClose">
				    <mu-menu>
				    	<span v-for="item in theme_radio_list" v-if="theme_radio_list.length">
				    		<mu-radio @change="changeTheme" name="group" :nativeValue="item.nativeValue" v-model="radio_value" :label="item.lable" style="" :labelClass="item.style" :iconClass="item.style"/><br/>
				    	</span>
				    </mu-menu>
				  </mu-popover>
			</mu-appbar>
			<mu-drawer :open="showDrawer" @close="toggle()">
				<mu-appbar style="height: 200px;" titleClass="titleClass">
				  <mu-icon-button icon="" slot="left"/>
				  <div id="">
				  	<img src="../assets/logo.png" style="width: 100px;"/>
				  	<div>
				  		Jiaqi
				  	</div>
				  </div>
				  <mu-flat-button label="" slot="right"/>
				  <mu-icon-button icon="" slot="right"/>
				</mu-appbar>
	      <mu-list @itemClick="toggle()">
	        <mu-list-item title="Menu Item 1"/>
	        <mu-list-item title="Menu Item 2"/>
	        <mu-list-item title="Menu Item 3"/>
	        <mu-list-item  @click.native="showDrawer = false" title="Close"/>
	      </mu-list>
	    </mu-drawer>
  	</div>
  	
		<router-view />
  </div>
</template>

<script>

import light from '!raw-loader!muse-ui/dist/theme-default.css'
import dark from '!raw-loader!muse-ui/dist/theme-dark.css'
import carbon from '!raw-loader!muse-ui/dist/theme-carbon.css'
import teal from '!raw-loader!muse-ui/dist/theme-teal.css'
export default {
  data () {
    return {
      theme: 'carbon',//主题
      themes: {
        light,
        dark,
        carbon,
        teal
      },
      radio_value:'carbon',//颜色选择
      theme_radio_list:[{nativeValue:'light',lable:'LIGHT',style:"radio-light"},{nativeValue:'dark',lable:'DARK',style:"radio-dark"},
      								{nativeValue:'carbon',lable:'CARBON',style:"radio-carbon"},{nativeValue:'teal',lable:'TEAL',style:"radio-teal"}],
      open: false,showDrawer:false,
      
      trigger: null
    }
  },
  mounted () {
    this.trigger = this.$refs.button.$el
  },
  methods: {
    changeTheme (theme) {
      this.theme = theme
      const styleEl = this.getThemeStyle()
      styleEl.innerHTML = this.themes[theme] || ''
    },
    getThemeStyle () {
      const themeId = 'muse-theme'
      let styleEl = document.getElementById(themeId)
      if (styleEl) return styleEl
      styleEl = document.createElement('style')
      styleEl.id = themeId
      document.body.appendChild(styleEl)
      return styleEl
    },
//  颜色选择、drawer菜单
     toggle (type) {
      if(type=='drawer'){
      	this.showDrawer=true
      }else if(type=='theme'){
      	this.open = !this.open
      }
    },
    handleClose (e) {
      this.open = false
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style >
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.radio-light {
	color: #673ab7 !important;
}
.radio-dark {
	color: black !important;
}
.radio-carbon {
	color: #607d8b !important;
}
.radio-teal {
	color: #009688 !important;
}
.titleClass{
	line-height: 1;
}
</style>



