<template>
	<div id="index">
		<div id="particles-js"></div>
		<!-- //用来包含粒子的DOM元素-->
		<mu-flexbox style="width: 100%;height: 100%; text-align: center;position: fixed;top: 0;z-index: 100;pointer-events:none">
	    <mu-flexbox-item class="flex-demo" >
	    	<!--style="width: 150px;height: 200px;position: fixed;top: 50%;transform: translateY(-50%);left: 50%;translateX(-50%);"-->
	      <div >
					<img src="../../build/logo.png" width="150px"/>
					<div id="">
						<mu-raised-button label="Jiaqi" primary class="demo-raised-button" style="pointer-events:painted"/>
					</div>
				</div>
	    </mu-flexbox-item>
    
  </mu-flexbox>
		
	</div>
</template>

<script>
	export default {
		data() {
			return {

			}
		},
		created() { //立即执行

		},
		methods: {
			drowParticles() {
				particlesJS('particles-js', {
					particles: {
						color: '#fff',
						shape: 'circle', // "circle", "edge" or "triangle"
						opacity: 0.7,
						size: 5,
						size_random: true,
						nb: 80,
						line_linked: {
							enable_auto: true,
							distance: 130,
							color: '#fff',
							opacity: 1,
							width: 1,
							condensed_mode: {
								enable: false,
								rotateX: 600,
								rotateY: 600
							}
						},
						anim: {
							enable: true,
							speed: 1
						}
					},
					interactivity: {
						enable: true,
						mouse: {
							distance: 300
						},
						detect_on: 'canvas', // "canvas" or "window"
						mode: 'grab',
						line_linked: {
							opacity: .5
						},
						events: {
							onclick: {
								enable: true,
								mode: 'push', // "push" or "remove"
								nb: 4
							}
						}
					},
					/* Retina Display Support */
					retina_detect: true
				});
			},
		},
		mounted() { //挂载完成执行
			this.drowParticles()
		},

	}
</script>

<style>
	#particles-js {
		width: 100%;
		height: 100%;
		background: #666;
		opacity: 0.7;
		/*// 背景色（不要设置为白色，因为粒子默认是白色）*/
		position: fixed;
		top: 0;
	}
</style>


