import Vue from 'vue'
import Router from 'vue-router'
import Tab from '@/components/Tab'
import index from '@/page/index'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Tab',
      component: Tab,
      children:[{path:'/',name:'index',component:index}]
    },
    
  ]
})



